	import listCell from '@/components/mix-list-cell';
    import {  
        mapState 
    } from 'vuex'; 
	
	import {
	     mapMutations  
	 } from 'vuex'; 
	let startY = 0, moveY = 0, pageAtTop = true;
    export default {
		components: {
			listCell
		},
		data(){
			return {
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				moving: false,
				footList:[]
			}
		},
		onLoad(){
			this.loadData();
		},




















        computed: {
			...mapState(['hasLogin','unifiedUser'])
		},
        methods: {
			...mapMutations(['login']),
			imgMemberPath:function(path){
				if(path==''||path==null||path=='null'){
					return "/static/missing-face.png"				 
				}else if (path!=''&&path.indexOf('http')>=0) {
					return path;				 
				}
				return this.siteHost + path;
			},
			imgPath:function(path){
				if(path==''||path==null||path=='null'){
					return "/static/errorImage.jpg"				 
				}else if (path!=''&&path.indexOf('http')>=0) {
					return path;				 
				}
				return this.siteHost + path;
			},
			async loadData() {
				var _this = this;
				if(this.hasLogin){
					//获取用户数据
					this.sendRequest({
						url : "get_user_info",
						data : {},
						method : "get",
						success:function (res) {
							if(res.code=='200'){
								_this.login({unifiedUser:res.data});
							} 
						}
					});  
					//获取足迹数据
					this.sendRequest({
						url : "get_my_foot_list",
						data : {pageNo:1,pageSize:10},
						method : "get",
						success:function (res) {
							if(res.code=='200'){ 
								let footList=res.data.datas;
								footList.forEach(item=>{
									item.data=JSON.parse(item.content);
								})
								_this.footList=footList;
							} 
						}
					}); 
				}
			},
			/**
			 * 统一跳转接口,拦截未登录路由
			 * navigator标签现在默认没有转场动画，所以用view
			 */
			navTo(url){
				if(!this.hasLogin){
					url = '/pages/public/login';
				}
				uni.navigateTo({  
					url
				})  
			}, 
	
			/**
			 *  会员卡下拉和回弹
			 *  1.关闭bounce避免ios端下拉冲突
			 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
			 *    transition设置0.1秒延迟，让css来过渡这段空窗期
			 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
			 */
			coverTouchstart(e){
				if(pageAtTop === false){
					return;
				}
				this.coverTransition = 'transform .1s linear';
				startY = e.touches[0].clientY;
			},
			coverTouchmove(e){
				moveY = e.touches[0].clientY;
				let moveDistance = moveY - startY;
				if(moveDistance < 0){
					this.moving = false;
					return;
				}
				this.moving = true;
				if(moveDistance >= 80 && moveDistance < 100){
					moveDistance = 80;
				}
		
				if(moveDistance > 0 && moveDistance <= 80){
					this.coverTransform = `translateY(${moveDistance}px)`;
				}
			},
			coverTouchend(){
				if(this.moving === false){
					return;
				}
				this.moving = false;
				this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
				this.coverTransform = 'translateY(0px)';
			}
        }  
    }  
